import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular'
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";


@IonicPage()
@Component({
  selector: 'page-i-recharge04',
  templateUrl: 'i-recharge04.html',
})
export class IRecharge04Page {

  mobileNo;
  amount;
  package;
  shortTitle;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public gaProvider : GoogleAnalyticsProvider) {

    this.mobileNo = navParams.get("mobileNo");
    this.amount = navParams.get("amount");
    this.package = navParams.get("package");
    this.shortTitle = navParams.get("shortTitle");
  }

  goToWelcome = function () {
    this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.popToRoot();
  };

  rechargeAnother = function () {
    this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.push('rechargeMobile01');
  };

  goAddToFavoriteList = function () {
    this.navCtrl.push('AddToFavorite01Page',{
      'mobileNo': this.mobileNo,
      'package': this.package,
      'shortTitle': this.shortTitle
    });
  };

  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("iRecharge Successful");
  }

}



